section
  padding: 50px 0
  @media ( max-width: 768px )
    padding: 25px 0

.title
  margin: 0
  margin-bottom: 40px
  font-family: "OpenSans-Bold", sans-serif
  font-weight: 700
  font-size: 30px
  line-height: 41px
  text-align: center
  @media ( max-width: 768px )
    font-size: 26px
    line-height: 35px
  @media ( max-width: 576px )
    margin-bottom: 10px
    font-size: 24px

.text
  margin: 0
  @media ( max-width: 768px )
    font-size: 18px
  @media ( max-width: 576px )
    font-size: 16px
